import vars from '../_vars';
import JustValidate from 'just-validate';

const validation = new JustValidate('.form',{
  errorLabelStyle: {
    color: '#ff8484 ',
    textDecoration: 'underlined',
  }
  });

validation
  .addField('#name', [
    {
      rule: 'required',
      errorMessage: 'Поле является обязательным',
    },
    {
      rule: 'minLength',
      value: 3,
      errorMessage: 'Минимум 3 символа',
    },
    {
      rule: 'maxLength',
      value: 50,
      errorMessage: 'Максимум 50 символов',
    },
  ])
  .addField('#link', [
    {
      rule: 'required',
      errorMessage: 'Поле является обязательным',
    },
    {
      rule: 'customRegexp',
      value: /\b((?:https?:\/\/|www\d{0,3}[.]|[a-z0-9.\-]+[.][a-z]{2,4}\/)(?:[^\s()<>]+|\(([^\s()<>]+|(\([^\s()<>]+\)))*\))+(?:\(([^\s()<>]+|(\([^\s()<>]+\)))*\)|[^\s`!()\[\]{};:'".,<>?«»“”‘’]))/i,
      errorMessage: 'Введите корректный URL',
    }
  ])
  .addField('#price', [
    {
      rule: 'required',
      errorMessage: 'Поле является обязательным',
    }
  ]);

  let name  = vars.$formName.value.length,
      url   = vars.$formURL.value.length,
      price = vars.$formName.value.length,
      btn   = vars.$formBtn;

  if(name && url && price) {
    btn.classList.add('form-btn--active');
  }
